body {
  margin: 0;
 padding: 0;
 background-repeat: no-repeat;
 background-size: cover;
 animation: changeBg 15s infinite;
max-height: 100%;
display: flex;
flex-direction: column;


 }
 #root{
overflow: scroll;
 
 }

 
 @keyframes changeBg{
  0%,100%  {background-image: url("https://cdn.pixabay.com/photo/2024/06/16/10/44/ai-generated-8833137_1280.jpg");}
  25% {background-image: url("https://cdn.pixabay.com/photo/2024/04/21/00/35/ai-generated-8709619_1280.jpg");}
  50% {background-image: url("https://cdn.pixabay.com/photo/2017/02/08/17/24/fantasy-2049567_1280.jpg");}
  75% {background-image: url("https://cdn.pixabay.com/photo/2022/10/12/20/31/flower-7517474_1280.jpg");}
  /* 100% {background-image: url("https://cdn.pixabay.com/photo/2023/09/09/10/25/mountains-8242910_1280.png");} */
  
}
